"use client";

import { motion } from "framer-motion";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Search, Camera, Mic, Hash, Sparkles, ArrowLeft, ShoppingBag } from "lucide-react";
import { useState, useEffect } from "react";
import Link from "next/link";
import { VinSearch } from "@/components/product/vin-search";
import { ProductSearch } from "@/components/product/product-search";

export function HeroSection() {
  const [searchQuery, setSearchQuery] = useState("");
  const [isMounted, setIsMounted] = useState(false);
  const [searchOpen, setSearchOpen] = useState(false);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  const handleSearchClick = () => {
    if (searchQuery.trim()) {
      setSearchOpen(true);
    }
  };

  const handleSearchKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Enter" && searchQuery.trim()) {
      setSearchOpen(true);
    }
  };

  return (
    <section className="relative min-h-[400px] sm:min-h-[500px] md:min-h-[600px] flex items-center justify-center overflow-hidden">

      {/* Minimal Grid Pattern - visible on all screens */}
      {/* Light mode grid */}
      <div 
        className="absolute inset-0 dark:hidden"
        style={{
          backgroundImage: `linear-gradient(var(--border) 1px, transparent 1px),
                          linear-gradient(90deg, var(--border) 1px, transparent 1px)`,
          backgroundSize: '50px 50px',
          opacity: 0.05,
        }}
      />
      {/* Dark mode grid - subtle and visible */}
      <div 
        className="absolute inset-0 hidden dark:block"
        style={{
          backgroundImage: `linear-gradient(var(--foreground) 1px, transparent 1px),
                          linear-gradient(90deg, var(--foreground) 1px, transparent 1px)`,
          backgroundSize: '50px 50px',
          opacity: 0.1,
        }}
      />

      {/* Main Content */}
      <div className="container relative z-10 px-3 sm:px-4 py-8 sm:py-12 md:py-16">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, ease: "easeOut" }}
          className="text-center space-y-4 sm:space-y-6 md:space-y-8"
        >
          {/* Minimal Search - Main focus */}
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.2, duration: 0.6 }}
            className="max-w-2xl sm:max-w-3xl md:max-w-4xl mx-auto"
          >
            <div className="glass-morphism-strong rounded-xl sm:rounded-2xl p-3 sm:p-4 md:p-6 shadow-2xl">
              <div className="flex gap-2 sm:gap-3">
                <ProductSearch
                  initialQuery={searchQuery}
                  isOpen={searchOpen}
                  onOpenChange={setSearchOpen}
                  trigger={
                    <Button 
                      size="default"
                      onClick={handleSearchClick}
                      className="bg-foreground hover:bg-foreground/90 text-background h-10 sm:h-11 md:h-12 px-3 sm:px-4 md:px-5 text-xs sm:text-sm md:text-base rounded-lg sm:rounded-xl font-medium whitespace-nowrap flex-shrink-0"
                    >
                      <ArrowLeft className="h-3.5 w-3.5 sm:h-4 sm:w-4 ml-1.5 sm:ml-2" />
                      <span>جستجو</span>
                    </Button>
                  }
                />
                <div className="flex-1 relative group">
                  <Search className="absolute right-2 sm:right-3 top-1/2 transform -translate-y-1/2 text-muted-foreground h-3.5 w-3.5 sm:h-4 sm:w-4 md:h-5 md:w-5 transition-colors group-focus-within:text-accent" />
                  <Input
                    type="text"
                    placeholder="جستجو..."
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                    onKeyDown={handleSearchKeyDown}
                    className="pr-8 sm:pr-10 md:pr-12 bg-background border-border/30 text-foreground placeholder:text-muted-foreground h-10 sm:h-11 md:h-12 text-xs sm:text-sm md:text-base rounded-lg sm:rounded-xl focus:border-accent/30 focus:ring-1 focus:ring-accent/20 transition-all"
                  />
                </div>
              </div>

              {/* Search Options - Compact */}
              <div className="flex flex-wrap gap-1.5 sm:gap-2 mt-3 sm:mt-4 justify-center">
                {[
                  { icon: Mic, label: "جستجوی صوتی", component: null },
                  { icon: Hash, label: "#جستجو با VIN", component: "vin" },
                  { icon: Camera, label: "جستجوی تصویری", component: null },
                ].map((option, index) => {
                  const Icon = option.icon;
                  
                  // If it's VIN search, use VinSearch component
                  if (option.component === "vin") {
                    return (
                      <motion.div
                        key={index}
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                      >
                        <VinSearch
                          trigger={
                            <Button
                              variant="ghost"
                              size="sm"
                              className="glass-morphism-light text-foreground hover:text-foreground hover:bg-foreground/20 text-[10px] sm:text-xs md:text-sm rounded-lg px-2 sm:px-3 py-1.5 sm:py-2 h-auto transition-all"
                            >
                              <Icon className="h-3 w-3 sm:h-3.5 sm:w-3.5 md:h-4 md:w-4 ml-1.5 sm:ml-2" />
                              <span className="whitespace-nowrap">{option.label}</span>
                            </Button>
                          }
                        />
                      </motion.div>
                    );
                  }
                  
                  return (
                    <motion.div
                      key={index}
                      whileHover={{ scale: 1.05 }}
                      whileTap={{ scale: 0.95 }}
                    >
                      <Button
                        variant="ghost"
                        size="sm"
                        className="glass-morphism-light text-foreground hover:text-foreground hover:bg-foreground/20 text-[10px] sm:text-xs md:text-sm rounded-lg px-2 sm:px-3 py-1.5 sm:py-2 h-auto transition-all"
                      >
                        <Icon className="h-3 w-3 sm:h-3.5 sm:w-3.5 md:h-4 md:w-4 ml-1.5 sm:ml-2" />
                        <span className="whitespace-nowrap">{option.label}</span>
                      </Button>
                    </motion.div>
                  );
                })}
                {/* دکمه خرید سریع */}
                <motion.div
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  <Link href="/chat">
                    <Button
                      variant="ghost"
                      size="sm"
                      className="glass-morphism-light text-foreground hover:text-foreground hover:bg-foreground/20 text-[10px] sm:text-xs md:text-sm rounded-lg px-2 sm:px-3 py-1.5 sm:py-2 h-auto transition-all"
                    >
                      <ShoppingBag className="h-3 w-3 sm:h-3.5 sm:w-3.5 md:h-4 md:w-4 ml-1.5 sm:ml-2" />
                      <span className="whitespace-nowrap">خرید سریع</span>
                    </Button>
                  </Link>
                </motion.div>
              </div>
            </div>
          </motion.div>

          {/* CTA Buttons - Horizontal and Minimal */}
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.3, duration: 0.6 }}
            className="flex flex-row gap-2 sm:gap-3 justify-center mt-4 sm:mt-6"
          >
            <Link href="/products">
              <Button 
                size="default"
                className="bg-foreground text-background hover:bg-foreground/90 h-9 sm:h-10 md:h-11 px-4 sm:px-5 md:px-6 text-xs sm:text-sm rounded-lg sm:rounded-xl font-medium transition-all whitespace-nowrap"
              >
                مشاهده محصولات
              </Button>
            </Link>
            <Link href="/guide">
              <Button 
                size="default"
                variant="outline" 
                className="glass-morphism-light h-9 sm:h-10 md:h-11 px-4 sm:px-5 md:px-6 text-xs sm:text-sm rounded-lg sm:rounded-xl text-foreground hover:bg-foreground/10 transition-all whitespace-nowrap"
              >
                راهنمای خرید
              </Button>
            </Link>
          </motion.div>
        </motion.div>
      </div>

    </section>
  );
}
